@charset 'UTF-8';
@use "sass:math";
@use "../global" as *;

/*
  Spacer
-----------------------------------------------------*/
.l-spacer {
  @each $device, $breakpoint in $breakpoints {
    @media screen and (min-width: #{math.div($breakpoint,16px) * 1em}) {
      @each $size, $val in $spaces {
        &[data-y~="#{$device}:#{$size}"] {
          padding: pxToRem($val) 0;
        }

        &[data-y~="#{$device}:#{$size}Top"] {
          padding-top: pxToRem($val);
        }

        &[data-y~="#{$device}:#{$size}Bottom"] {
          padding-bottom: pxToRem($val);
        }

        &[data-x~="#{$device}:#{$size}"] {
          padding: 0 pxToRem($val);
        }

        &[data-x~="#{$device}:#{$size}Right"] {
          padding-right: pxToRem($val);
        }

        &[data-x~="#{$device}:#{$size}Left"] {
          padding-left: pxToRem($val);
        }
      }

      &[data-reset~="#{$device}:all"] {
        padding: 0;
      }

      &[data-reset~="#{$device}:top"] {
        padding-top: 0;
      }

      &[data-reset~="#{$device}:right"] {
        padding-right: 0;
      }

      &[data-reset~="#{$device}:bottom"] {
        padding-bottom: 0;
      }

      &[data-reset~="#{$device}:left"] {
        padding-left: 0;
      }
    }
  }

  &.-line {
    border-bottom: 1px solid $primary-border-color;
  }

  &.-moveUp {
    $pc-comp-moveUp: 144px;
    $sp-comp-moveUp: 94.5px;
    margin-top: pxToRem(-($sp-comp-moveUp + $footer-sp-space));

    @include mq(md) {
      margin-top: pxToRem(-($pc-comp-moveUp + $footer-pc-space));
    }
  }

  &[data-bg~="min:dark"] {
    background-color: $main-color;
  }

  &[data-bg~="min:thinGrey"] {
    background-color: #fafafa;
  }

  &[data-bg~="lg:lightGreyHalf"] {
    @include mq(lg) {
      position: relative;

      &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        right: 0;
        bottom: 0;
        width: 50%;
        background-color: #f0f0f0;
      }
    }
  }

  &.-bgMain {
    background-color: $main-color;
  }

  &.-overflow {
    overflow: hidden;
  }
}
