@charset 'UTF-8';
@use "sass:math";
@use "sass:map";
@use "../../global" as *;

.p-intro {
  display: none;

  .is-useJs & {
    display: flex;
    position: fixed;
    z-index: 9999;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
  }

  &::before {
    content: "";
    position: fixed;
    z-index: 1;
    width: 100vw;
    height: 100vh;
    background-color: #fff;
  }

  &__text {
    display: inline-block;
    position: relative;
    z-index: 2;
    padding: 0.1em;
    animation-name: slideBorder;
    animation-duration: 1s;
    animation-timing-function: cubic-bezier(0.3, 1, 0.6, 0.98);
    animation-iteration-count: infinite;
    background-image: linear-gradient($main-color 0 100%);
    background-repeat: no-repeat;
    background-position: 0 100%;
    background-size: 0% 2px;
    font-size: 2.5rem;
    font-weight: 700;

    @include mq(xxl) {
      font-size: 4rem;
    }
  }

  @keyframes slideBorder {
    0% {
      background-position: left bottom;
      background-size: 0% 2px;
    }

    50% {
      background-position: left bottom;
      background-size: 100% 2px;
    }

    50.1% {
      background-position: right bottom;
    }

    60% {
      background-position: right bottom;
      background-size: 100% 2px;
    }

    100% {
      background-position: right bottom;
      background-size: 0% 2px;
    }
  }

  .is-loaded &__text {
    visibility: hidden;
    z-index: -9999;
    transition-property: opacity, visibility;
    transition-duration: 0.3s;
    opacity: 0;
    pointer-events: none;
  }

  .is-loaded &::before {
    z-index: -9999;
    transform: scaleX(0);
    animation-name: slideBg;
    animation-duration: 1s;
    animation-timing-function: cubic-bezier(0.5, 0.91, 0.53, 0.98);
    animation-delay: 0.3s;
    background-color: $main-color;
    pointer-events: none;
    animation-fill-mode: forwards;
  }

  @keyframes slideBg {
    0% {
      transform: scaleX(0);
      transform-origin: left;
      opacity: 0;
      background-color: $main-color;
    }

    50% {
      transform: scaleX(1);
      transform-origin: left;
      background-color: $main-color;
    }

    50.1% {
      transform-origin: right;
    }

    60% {
      transform: scaleX(1);
      transform-origin: right;
    }

    100% {
      transform: scaleX(0);
      transform-origin: right;
      background-color: $main-color;
    }
  }

  .is-over & {
    display: none;
  }
}
