@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

/*
  Button
*/
.c-button {
  display: inline-block;
  border: 0;
  background-color: transparent;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  appearance: none;
}

.c-button.-skip[href]:focus {
  padding: 1rem;
  border: 1px solid #000;
  border-radius: 9999px;
  color: #fff;
}

.c-button.-primary {
  width: 100%;
  max-width: pxToRem(160px);
  padding: pxToRem(16px);
  transition-property: background-size, color;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.42, 0, 0.55, 0.24);
  border: 1px solid $primary-border-color;
  border-radius: 9999px;
  background-image: linear-gradient($base-color 0% 100%);
  background-repeat: no-repeat;
  background-size: 0% 100%;
  color: #fff;
  font-size: pxToRem(16px);
  font-weight: 700;
  background-position-x: right;

  @include hover() {
    background-size: 100% 100%;
    color: #000;
    background-position-x: left;
  }

  @include mq(sm) {
    max-width: pxToRem(220px);
  }

  @include mq(lg) {
    padding: pxToRem(24px);
  }
}

.c-button.-secondary {
  width: 100%;
  max-width: pxToRem(180px);
  padding: pxToRem(16px);
  transition-property: background-size, color;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.42, 0, 0.55, 0.24);
  border: 1px solid $main-color;
  border-radius: 9999px;
  background-color: $main-color;
  background-image: linear-gradient($base-color 0% 100%);
  background-repeat: no-repeat;
  background-size: 0% 100%;
  color: $base-color;
  font-size: pxToRem(14px);
  font-weight: 700;
  text-align: center;
  background-position-x: right;

  @include hover() {
    background-size: 100% 100%;
    color: #000;
    background-position-x: left;
  }

  @include mq(sm) {
    max-width: pxToRem(220px);
    font-size: pxToRem(16px);
  }

  @include mq(lg) {
    max-width: pxToRem(175px);
  }

  @include mq(xxl) {
    max-width: pxToRem(200px);
    padding: pxToRem(24px);
    font-size: pxToRem(18px);
  }
}
