@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

.p-box {
  $this: &;

  &.-feature {
    display: flex;
    z-index: 2;
    flex-direction: column;
    flex-grow: 1;
    align-items: center;
    padding: pxToRem(72px) pxToRem(34px) pxToRem(64px);
    transition-property: transform;
    transition-duration: 0.5s;
    background-color: #fff;
    box-shadow: 0 0 24px rgba(#000, 0.04);
    text-align: center;

    @include hover() {
      transform: translateY(10px);
    }

    & #{$this}__term {
      margin-top: pxToRem(25px);
      font-size: pxToRem(24px);
      font-weight: 700;
      letter-spacing: 0.04em;
      line-height: 1.2;
    }

    & #{$this}__description {
      margin-top: pxToRem(25px);
      font-size: pxToRem(12px);
      font-weight: 700;
      letter-spacing: 0.04em;
      line-height: 2;

      @include mq(xxl) {
        @include ff(12px, 16px, 1400px, 2500px);
      }
    }
  }
}
