@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

/*
  Heading
*/
.c-heading {
  display: block;
  font-weight: 700;

  &[data-align="right"] {
    text-align: right;
  }

  &[data-align="center"] {
    text-align: center;
  }

  &.-level2 {
    @include ff(32px, 64px);
    letter-spacing: 0.03em;
    line-height: 1.2;

    &.is-inviewReady {
      clip-path: inset(0 100% 0 0);
    }

    &.is-inviewActive {
      transition-property: clip-path;
      transition-duration: 2s;
      transition-timing-function: linear;
      clip-path: inset(0 0 0 0);
    }
  }

  &.-level3 {
    @include ff(24px, 48px);
    line-height: 1.2;
  }
}
