@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

/*
  Hamburger
*/
.p-hamburger {
  $this: &;

  --hamburgerBorder: linear-gradient(#fff 0 100%);
  display: block;
  position: relative;
  width: 34px;
  height: 34px;
  padding: 0;

  border: 2px solid #fff;
  background-color: #fff;

  &::before {
    //背景が白色になるように
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: #000;
  }

  @include mq(lg) {
    display: none;
  }

  &__line {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: calc((18 / 34) * 100%);
    height: 2px;
    margin: auto;
    background-color: $base-color;

    &::before,
    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      transition-property: transform;
      transition-duration: 0.3s;
      background-color: $base-color;
    }

    &::before {
      transform: translateY(-5px);
    }

    &::after {
      transform: translateY(5px);
    }
  }

  .is-drawerActive & {
    &__line {
      background-color: transparent;
    }
  }

  &[aria-expanded="true"] &__line::before {
    transform: rotate(45deg);
  }

  &[aria-expanded="true"] &__line::after {
    transform: rotate(-45deg);
  }
}
