@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

/*
  Footer
*/
.p-footer {
  $this: &;
  position: relative;
  padding: pxToRem($footer-sp-space) 0;
  background-color: $main-color;
  color: $base-color;

  .is-useJs & {
    opacity: 0;
  }

  .is-over & {
    transition-property: opacity;
    transition-duration: 0.5s;
    opacity: 1;
  }

  @include mq(md) {
    padding: pxToRem($footer-pc-space) 0;
  }

  &__text {
    font-size: max(pxToVw(21px), pxToRem(10px));
    font-weight: 700;
    line-height: 2;
    text-align: center;

    @include mq(xs) {
      @include ff(21px, 32px);
    }

    &.is-inviewReady {
      transform: scale(0);
    }

    &.is-inviewActive {
      transform: scale(1);
      transition-property: transform;
      transition-duration: 0.5s;
    }
  }

  &__action {
    text-align: center;

    & #{$this}__button {
      @include mq(md) {
        @include ff(16px, 24px);
      }

      @include mq(lg) {
        max-width: pxToRem(230px);
      }
    }
  }
}
