@charset 'UTF-8';
@use "sass:math";
@use "../global" as *;

/*
  Main
-----------------------------------------------------*/
.l-main {
  clear: both;

  .is-useJs & {
    opacity: 0;
  }

  .is-over & {
    transition-property: opacity;
    transition-duration: 0.5s;
    transition-delay: 0.5s;
    opacity: 1;
  }
}
