@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

.p-section {
  $this: &;

  &__header {
    text-align: center;
  }

  &__text {
    margin-top: pxToRem(20px);
    font-weight: 700;
    letter-spacing: 0.04em;
    line-height: 2;

    @include mq(xxs) {
      @include ff(14px, 16px, 360, 1400px);
    }

    @include mq(md) {
      margin-top: pxToRem(32px);
    }

    @include mq(xxl) {
      @include ff(16px, 18px, 1400px, 2500px);
    }
  }

  &.-feature {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      width: 100%;
      min-height: 420px;
      background-color: #101010;

      @include mq(xl) {
        bottom: 0;
        width: 40%;
        min-height: auto;
      }
    }

    & #{$this}__header {
      z-index: 2;
      color: $base-color;
      text-align: left;

      @include mq(lg) {
        text-align: center;
      }

      @include mq(xl) {
        text-align: left;
      }
    }

    & #{$this}__text {
      @include mq(md) {
        margin-top: pxToRem(43px);
      }
    }
  }

  &.-flexible {
    & #{$this}__header {
      text-align: left;

      @include mq(lg) {
        padding: 0 pxToRem(36px);
      }
    }

    & #{$this}__text {
      letter-spacing: 0.1em;
    }
  }

  &.-gutter {
    & #{$this}__header {
      text-align: left;

      @include mq(lg) {
        padding: 0 pxToRem(36px);
      }
    }

    & #{$this}__text {
      letter-spacing: 0.1em;
    }
  }

  &.-alignment {
    & #{$this}__header {
      color: $base-color;
    }

    & #{$this}__body {
      padding-top: pxToRem(24px);

      @include mq(md) {
        padding-top: pxToRem(120px);
      }
    }
  }

  &.-normal,
  &.-reverse {
    & #{$this}__header {
      margin-bottom: pxToRem(12px);

      @include mq(md) {
        margin-bottom: pxToRem(24px);
      }
    }

    & #{$this}__number {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: pxToRem(48px) 0;
      font-weight: 700;
    }
  }

  &.-normal {
    & #{$this}__header {
      text-align: left;
    }
  }

  &.-reverse {
    & #{$this}__header {
      text-align: right;
    }
  }
}
