@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

/*
  Axis
*/

.p-axis {
  $this: &;

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: $base-color;
    text-align: center;
  }

  &__text {
    font-size: pxToRem(12px);
    font-weight: 700;
  }

  &.-main {
    & #{$this}__content {
      padding: pxToRem(32px) pxToRem(8px);

      @include mq(md) {
        padding: pxToRem(60px) pxToRem(16px);
      }
    }

    & #{$this}__text {
      margin-top: pxToRem(10px);

      @include mq(md) {
        margin-top: pxToRem(16px);
      }
    }
  }

  &.-cross {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    min-height: 108px;

    @include mq(md) {
      min-height: auto;
    }

    & #{$this}__content {
      flex-grow: 1;
      padding: pxToRem(10px) 0;

      @include mq(md) {
        padding: pxToRem(16px) 0;
      }
    }

    & #{$this}__text {
      margin-top: pxToRem(6px);

      @include mq(md) {
        margin-top: pxToRem(10px);
      }
    }
  }
}
