@charset 'UTF-8';
@use "sass:math";
//============================================
//  Layout Settings
//============================================

// Contentの幅
$content-width: 1200px !default;

// ファーストビューの高さ
$first-view: 768px;

// デザインカンプのアートボードサイズ
$sp-comp-width: 375px;
$tab-comp-width: 768px;
$pc-comp-width: 1366px;

//メインビジュアルの画像の幅
$hero-img-width: 549px;

// ブレークポイント
$breakpoints: (
  min: 0.001px,
  xxs: 360px,
  xs: 414px,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
) !default;

// 余白
$spaces: (
  minimum: 8px,
  xxsmall: 16px,
  xsmall: 32px,
  small: 40px,
  medium: 64px,
  large: 88px,
  xlarge: 120px,
  xxlarge: 240px,
) !default;

$footer-pc-space: 56px;
$footer-sp-space: 64px;

// Grid Column
$column: 12;

$gaps: (
  xxsmall: 8px,
  xsmall: 16px,
  small: 24px,
  medium: 30px,
  large: 40px,
  xlarge: 48px,
  xxlarge: 60px,
  maximum: 100px,
) !default;

//* =======================================================
//* Layer Setting
//* =======================================================

$layer: (
  "modal": 1100,
  "drawer": 1040,
  "sp-menu": 1035,
  "floating": 1030,
  "header": 1020,
  "default": 1,
  "negative": -1,
) !default;
