@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

/*
  Header
*/
.p-header {
  .is-useJs & {
    opacity: 0;
  }

  .is-over & {
    opacity: 1;
  }

  &__inner {
    position: fixed;
    @include ly(header);
    top: 0;
    right: 0;
    left: 0;
    mix-blend-mode: difference;

    @include mq(lg, true) {
      .is-drawerActive & {
        mix-blend-mode: normal;
      }

      &::before {
        content: "";
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        transform: scaleX(0);
        transition-property: transform, opacity;
        transition-duration: 0.5s, 0.2s;
        transition-timing-function: cubic-bezier(0.3, 1, 0.6, 0.98);
        opacity: 0;
        background-color: #000;
      }

      .is-drawerActive &::before {
        transform: scaleX(1);
        transform-origin: left top;
        opacity: 1;
      }
    }
  }

  &__info {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-top: pxToRem(48px);

    @include mq(lg, true) {
      .is-drawerActive & {
        mix-blend-mode: difference;
      }
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $base-color;

    & a {
      @include ff(20px, 32px, 375px, 992px);

      @include mq(lg) {
        font-size: pxToRem(32px);
      }
    }
  }

  &__nav {
    @include mq(lg, true) {
      position: absolute;
      top: pxToRem(66px);
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      opacity: 0;
      pointer-events: none;

      @include ly(negative);
    }

    .is-drawerActive & {
      @include ly(drawer);
      opacity: 1;
      pointer-events: auto;
    }
  }
}
