@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

/*
  Sort
*/

.p-sort {
  &__header {
    margin-bottom: pxToRem(12px);

    @include mq(md) {
      margin-bottom: pxToRem(24px);
    }
  }

  &__number {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: pxToRem(48px) 0;
    font-weight: 700;
  }
}
