@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

/*
  Figure
*/

.p-figure {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;

  &.is-inviewReady {
    opacity: 0;
  }

  &.is-inviewActive {
    transition-property: opacity;
    transition-duration: 0.5s;
    opacity: 1;
  }

  & img {
    width: 100%;
    height: 100%;
    filter: grayscale(1);
    transition-property: transform;
    transition-duration: 0.3s;
    transition-timing-function: cubic-bezier(0.06, 0.17, 0.22, 0.55);
  }

  @include hover() {
    & img {
      transform: scale(1.2);
      filter: none;
    }
  }
}
