@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

/*
  Global Nav
*/
.p-globalNav {
  display: flex;
  flex-direction: column;
  // align-items: left;
  // justify-content: center;

  @include mq(lg) {
    flex-direction: row;
    align-items: center;
  }

  &__item {
    color: $base-color;
    font-weight: 700;
    letter-spacing: 0.04em;
    @include ff(16px, 24px, 0, 360px);

    @include mq(xxs) {
      font-size: pxToRem(32px);
    }

    @include mq(lg, true) {
      transform: translateX(-1rem);
      opacity: 0;

      .is-drawerActive & {
        transform: translateX(0);
        transition-property: opacity, transform;
        transition-duration: 0.5s;
        transition-timing-function: cubic-bezier(0.3, 1, 0.6, 0.98);
        opacity: 1;
      }

      .is-drawerActive &:nth-child(1) {
        transition-delay: 0.5s;
      }

      .is-drawerActive &:nth-child(2) {
        transition-delay: 0.6s;
      }

      .is-drawerActive &:nth-child(3) {
        transition-delay: 0.7s;
      }

      .is-drawerActive &:nth-child(4) {
        transition-delay: 0.8s;
      }

      .is-drawerActive &:nth-child(5) {
        transition-delay: 0.9s;
      }
    }

    @include mq(lg) {
      font-size: pxToRem(16px);
    }

    & + & {
      @include mq(lg, true) {
        margin-top: pxToRem(40px);
      }

      @include mq(lg) {
        margin-left: pxToRem(24px);
      }

      @include mq(xxl) {
        margin-left: pxToRem(40px);
      }
    }

    & a {
      display: inline-block;

      @include mq(xxl) {
        @include ff(16px, 18px, 1400px, 2500px);
      }
    }

    & a:not(.c-button) {
      padding: 8px 0;
      transition-property: background-size;
      transition-duration: 0.3s;
      transition-timing-function: cubic-bezier(0.42, 0, 0.55, 0.24);
      background-image: linear-gradient($base-color 0% 100%);
      background-repeat: no-repeat;
      background-position: right;
      background-size: 0% 0.2em;
      background-position-y: bottom;

      @include mq(lg, true) {
        width: 100%;
      }

      @include hover() {
        background-size: 100% 0.2em;
        background-position-x: left;
      }
    }

    &.-action {
      @include mq(lg, true) {
        align-self: center;
        margin-top: pxToRem(64px);
        margin-bottom: pxToRem(64px);
      }
    }

    &.-action a {
      max-width: 100%;
      padding: pxToRem(16px);

      @include mq(xxl) {
        padding: pxToRem(24px);
        @include ff(16px, 18px, 1400px, 2500px);
      }
    }
  }
}
