@charset 'UTF-8';
@use "sass:math";
@use "sass:map";
@use "../../global" as *;

/*
Hero
*/
.p-hero {
  $pc-globalNav-height: pxToRem(55px);
  $this: &;
  position: relative;
  overflow: hidden;

  &__inner {
    @include mq(lg, true) {
      padding-top: pxToRem(120px);
    }
  }

  &__body {
    margin-bottom: pxToRem(108px);
    padding-right: math.div(18px, $sp-comp-width) * 100vw;
    padding-left: math.div(18px, $sp-comp-width) * 100vw;

    @include mq(lg) {
      padding-top: calc(pxToRem(76px) + $pc-globalNav-height);
      padding-right: 0;
      padding-left: calc(((($pc-comp-width - $content-width) / 2) / $pc-comp-width) * 100vw);
    }

    @include mq(xxl) {
      justify-content: center;
      padding-top: pxToRem(108px) + $pc-globalNav-height;
    }
  }

  &__title {
    font-size: 32px;
    font-size: clamp(32px, pxToVw(44px, 360px), pxToRem(44px));
    font-weight: 700;
    letter-spacing: 0.04em;
    line-height: 1.2;

    @include mq(xxs) {
      font-size: 44px;
    }

    @include mq(xs) {
      font-size: pxToVw(60px, 576px);
    }

    @include mq(sm) {
      font-size: pxToVw(77px, 768px);
    }

    @include mq(md) {
      // margin: 0 auto;
      font-size: pxToVw(100px, 992px);
    }

    @include mq(lg) {
      font-size: pxToVw(68px, 1200px);
    }

    @include mq(xl) {
      font-size: pxToVw(80px, 1366px);
    }

    @include mq(xxl) {
      font-size: 130px;
      font-size: clamp(80px, pxToVw(130px, 2500px), 130px);
    }
  }

  .is-over &__string span {
    display: inline-block;
    transform: translateX(pxToRem(-40px));
    animation-name: stringScale;
    animation-duration: 1s;
    animation-timing-function: cubic-bezier(0.18, 0.66, 0.75, 0.98);
    opacity: 0;
    animation-fill-mode: forwards;

    @keyframes stringScale {
      0% {
        transform: translateX(pxToRem(-40px));
        opacity: 0;
      }

      99% {
        transform: translateX(0);
        opacity: 1;
      }

      100% {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }

  $chars: 13;

  @for $i from 1 through $chars {
    .is-over &__string > span:nth-child(#{$i}) {
      animation-delay: $i * 0.1 + s;
    }
  }

  &__description {
    margin-top: pxToRem(24px);
    font-weight: 700;
    letter-spacing: 0.04em;
    line-height: 2;

    @include ff(14px, 16px, 0, 1400px);

    @include mq(sm) {
      margin-top: pxToRem(48px);
      @include ff(16px, 20px, map.get($breakpoints, sm), map.get($breakpoints, lg));
    }

    @include mq(lg) {
      margin-top: pxToRem(32px);
      font-size: pxToRem(16px);
    }

    @include mq(xl) {
      margin-top: pxToRem(64px);
    }

    @include mq(xxl) {
      @include ff(16px, 20px, 1400px, 2500px);
      letter-spacing: 0.08em;
    }
  }

  &__action {
    margin-top: pxToRem(40px);
    text-align: center;

    @include mq(lg) {
      margin-top: pxToRem(48px);
    }

    @include mq(xxl) {
      margin-top: pxToRem(100px);
    }
  }

  &__galleries.-bgDarkHalf {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100%;
    padding: pxToRem(40px) calc((((($pc-comp-width / 2) - $hero-img-width) / 2) / $pc-comp-width) * 100vw);
    background-color: #101010;

    @include mq(lg) {
      padding-top: pxToRem(76px) + $pc-globalNav-height;
      padding-bottom: pxToRem(76px);
    }

    @include mq(xxl) {
      padding-top: pxToRem(108px) + $pc-globalNav-height;
      padding-bottom: pxToRem(108px);
    }
  }

  &__thumbnail {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }

  &__picture {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }

  &__picture img {
    flex-grow: 1;
    width: 100%;
    filter: grayscale(1);
  }

  .is-over &__picture img {
    animation-name: imageFadeIn;
    animation-duration: 0.5s;
    animation-timing-function: cubic-bezier(0.69, 0.05, 0.82, 0.5);
    animation-delay: 1s;
    opacity: 0;
    animation-fill-mode: forwards;

    @keyframes imageFadeIn {
      0% {
        opacity: 0;
      }

      100% {
        opacity: 1;
      }
    }
  }
}
