@charset 'UTF-8';
@use "sass:math";
@use "../global" as *;

/*
  Container
-----------------------------------------------------*/
.l-container {
  width: 90%;
  max-width: $content-width;
  margin: 0 auto;
}

// .l-container.-xxlNarrow {
//   @include mq(xxl) {
//     max-width: 1000px;
//   }
// }

.l-container.-xxlSpread {
  @include mq(xxl) {
    max-width: math.div($content-width, $pc-comp-width) * 100%;
    max-width: clamp($content-width, math.div($content-width, $pc-comp-width) * 100vw, 100%);
  }
}
