@charset 'UTF-8';
@use "sass:math";
@use "../../global" as *;

/*
  Logo
*/
.p-logo {
  $this: &;

  &.-secondary {
    @include ff(24px, 48px);
    font-weight: 700;
    line-height: 1.2;
    text-align: center;

    & small {
      display: block;
      padding-top: pxToRem(8px);
      font-size: pxToRem(10px);
    }
  }
}
